import React, { Component } from "react"
import { Button, Form } from "antd"

import BraftEditor from "braft-editor"
import "braft-editor/dist/index.css"
import api from "../../../api"

type StateType = {
  editorState: any;
  question:any
};
type propType = {
  question: any;
};

class AnswerModal extends Component<propType, StateType> {
    constructor(props: propType) {
      super(props)
      this.state = {
        editorState: BraftEditor.createEditorState(null),
        question:{}
      }
    }

  async componentDidMount () {
    // 假设此处从服务端获取html格式的编辑器内容
    // 使用BraftEditor.createEditorState将html字符串转换为编辑器需要的editorStat
    this.setState({
        editorState: BraftEditor.createEditorState(""),
        question:this.props.question
    })
  }

  submitContent = async () => {
    // 在编辑器获得焦点时按下ctrl+s会执行此方法
    // 编辑器内容提交到服务端之前，可直接调用editorState.toHTML()来获取HTML格式的内容
    const htmlContent = this.state.editorState.toHTML()
    console.log(htmlContent)
    // const result = await saveEditorContent(htmlContent)
  }

  handleEditorChange = (editorState: any) => {
    this.setState({ editorState })
  }

  handleConfirm = () => {
      api.newAnswer({
        questionId:this.state.question.id,
        authorId: 2,
        mainPic:"/api/static/media/1.b4b91e14.jpg",
        content: this.state.editorState.toHTML()
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
  }

  render () {
    const { editorState,question } = this.state
    return (
      <Form
        name="basic"
        labelCol={{ span: 3 }}
        wrapperCol={{ span: 18 }}
        onFinish={this.handleConfirm}
        onFinishFailed={(err) => { console.log(err) }}
      >
        <Form.Item
          label="问题"
        >
          {question.title}
        </Form.Item>

        <Form.Item
            label="问题描述"
        >
            <div style={{ paddingTop:5, textIndent: 2 }} dangerouslySetInnerHTML={{ __html:question.content }}></div>
        </Form.Item>

        <Form.Item
          label="您的回答"
          name="content"
          rules={[{ required: true, message: "Please input your password!" }]}
        >
          <BraftEditor
            value={editorState}
            onChange={this.handleEditorChange}
            onSave={this.submitContent}
          />
        </Form.Item>

        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
        </Form.Item>
      </Form>
      )
  }
}

export default AnswerModal
